import React from "react";
import '../../styles/profile.scss';
import { Card } from "react-bootstrap";
import Image from 'react-bootstrap/Image';
import Badge from 'react-bootstrap/Badge';
import '../../styles/searchCard.scss';
import DEFAULT_PROFILE from '../../images/default-profile.png';

export default function ProfileCard({ profile }) {
  return (
    <Card className="search-card" bg="secondary">
      <a href={`/#/profile/${profile.username}`} style={{ color: 'inherit', textDecoration: 'inherit' }}>
        <Card.Body>
          <Card.Header className="card-header">
            <span>
              <Image
                src={profile.pictureUrl || DEFAULT_PROFILE}
                roundedCircle
              />
              {profile.username}
            </span>
          </Card.Header>
          <Card.Text>
            {profile.bio}
          </Card.Text>
        </Card.Body>

        <Card.Footer>
          {profile.interests.length > 0 && (
            <>
              <b>Interests: </b>
              {profile.interests.map(interest => (
                <Badge pill key={interest} variant="dark">
                  {interest}
                </Badge>
              ))}
              <br />
            </>
          )}
          
          {profile.languages.length > 0 && (
            <>
              <b>Skills: </b>
              {profile.languages.map(language => (
                <Badge pill key={language} variant="dark">
                  {language}
                </Badge>
              ))}
            </>
          )}
        </Card.Footer>
      </a>
    </Card>
  );
}