import React from 'react';
import { Helmet } from 'react-helmet';
import { withRouter } from 'react-router-dom';
import ProjectCard from '../components/project/projectCard';
import Loading from '../components/loading';
import { withContext } from '../components/layout';
import { Container, Row, Col, FormControl, Button } from "react-bootstrap";
import ProjectController from '../controllers/projectController';
import UserController from "../controllers/userController";
import { debounce } from 'throttle-debounce';
import '../styles/projects.scss';

const DEBOUNCE_MS = 200;

class Projects extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      projects: [],
      search: '',
      finishedLoading: false
    };

    this.retrieveProjects = debounce(
      DEBOUNCE_MS,
      this.retrieveProjects.bind(this)
    );
    this.updateSearch = this.updateSearch.bind(this);
    this.renderHeader = this.renderHeader.bind(this);
    this.renderProjects = this.renderProjects.bind(this);
    this.renderCreateButton = this.renderCreateButton.bind(this);
  }

  async componentDidMount() {
    this.retrieveProjects();
  }

  async retrieveProjects(search) {
    this.setState({ finishedLoading: false });

    try {
      const projects = search
        ? await ProjectController.getFilteredProjects(search)
        : await ProjectController.getProjects();

      this.setState({ projects });
    } catch (error) {
      console.log(error);
    }

    this.setState({ finishedLoading: true });
  }

  updateSearch(search) {
    this.setState({ search });
  }

  renderCreateButton() {
    return (
      <Button
        onClick={() => {
          if (UserController.isLoggedIn()) {
            window.history.pushState({}, '', '/#/projects/create');
            window.location.reload();
          } else {
            const { showLoginModal } = this.props;
            showLoginModal('/#/projects/create');
          }
        }}
        className="add-project-btn"
        variant="outline-light"
        title="Create a project"
      >
        +
      </Button>
    );
  }

  renderHeader() {
    const { search } = this.state;

    return (
      <>
        <h1>
          <span></span> Projects
            {this.renderCreateButton()}
        </h1>
        <p>
          Discover projects and find one to join!
        </p>
        <FormControl
          placeholder="Search by title, technologies, etc..."
          className="search"
          value={search}
          type="text"
          size="lg"
          onChange={async (event) => {
            const search = event.target.value;
            this.updateSearch(search);
            await this.retrieveProjects(search);
          }}
        />
      </>);
  }

  renderProjects() {
    const { projects, finishedLoading } = this.state;

    if (!finishedLoading) {
      return <Loading />
    }

    if (projects.length === 0) {
      return <h4>No projects match the search.</h4>;
    }

    return (
      <Row>
        {projects.map(p => (
          <Col sm="4" key={p.id}>
            <ProjectCard project={p} />
          </Col>
        ))}
      </Row>
    );
  }

  render() {
    return (
      <>
        <Helmet>
          <title>Projects</title>
        </Helmet>
        <Container fluid>
          {this.renderHeader()}
          {this.renderProjects()}
        </Container>
      </>
    );
  }
}

export default withContext(withRouter(Projects), 'projects-page');